:root {
  --text--white: #fff;
  --hero-background: #ff3333;
  --primary-bg-color: #123524; /* Similar dark green background */
  --input-bg-color: #1d4731;
  --input-border-color: #ffffff33;
  --button-bg-color: #ffffff;
  --button-text-color: #123524;
}

body {
  font-family: "popins", sans-serif;
  color: var(--text--white);
  background-color: var(--primary-bg-color);
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px;
  text-align: center;
  z-index: 1000;
}

.nav-component ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.nav-component ul li a {
  font-size: 1.2rem;
  padding: 10px;
  text-decoration: none;
  color: var(--text--white);
}

.nav-end {
  display: flex;
}

@media (min-width: 768px) {
  .nav-component ul {
    margin: 0 5rem;
  }
}


footer {
  background-color: #ffefef;
  padding: 2rem;
  text-align: center;
}

footer a {
  text-decoration: none;
  color: #000;
}

@media (min-width: 768px) {
  .contact-section {
    padding: 4rem 10rem;
  }

  .left-side h1 {
    font-size: 4rem;
  }

  .left-side {
    padding-right: 4rem;
  }

  footer {
    display: flex;
    justify-content: space-between;
    padding: 2rem 5rem;
  }
}
